<template>
  <div>
            <van-nav-bar
        title="标题"
        left-text="我的发布"
        left-arrow
        @click-left="onClickLeft"
        />
        <div v-for="(item,index) in myfabulist" :key="index" class="mylist">
            <h3>用户名:{{item.author}}</h3>
            <li>分类名:{{item.cate_name}}</li>
            <li>内容:{{item.title}}</li>
            <li>发布时间:{{ new Date(parseInt(item.time)).toLocaleString().replace(/:\d{1,2}$/,'')}}<button @click="shanchu(index)">删除</button></li>
            <img :src="item.imageSrc" alt="">
        </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    name:"myfabu",
    data() {
        return {
            myfabulist:[],
            myuid:this.$route.query.myfabu2
        }
    },
    created(){
       this.mybtn()
    },
    methods:{
        onClickLeft() {
      this.$router.go(-1)
    },
    mybtn(){
        axios.get(`https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_user_article_list?uid=${this.myuid}&skip&limit`)
                    .then((res) => {
                console.log(res.data.data);
                this.myfabulist=res.data.data
            })
    },
    shanchu(val){
        axios.post("https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/del_user_article",{
            _id:this.myfabulist[val]._id,
            uid:this.uid
        }).then((res) => {
            // console.log(res.data.data);
            console.log(res);
            this.mybtn()
          });
    }
    },
}
</script>

<style>
.mylist{width: 90%;padding: 10px;border: 1px solid #ededed;}
</style>