<template>
  <div>
    <form action="/">
        <van-search
          v-model="value"
          show-action
          background="#4fc08d"
          placeholder="请输入搜索关键词"
          @search="onSearch"
          @cancel="onCancel"
          @input="mjk"
        />
      </form>
      <ul v-for="(item,index) in list" :key="index">
        <li @click="lilist(index)"><i class="iconfont icon-sousuo">{{item.title}}</i></li>
      </ul>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    name:'sousuo',
    data() {
    return {
      value: '',
      list:[]
    };
  },
  methods: {
    onSearch() {
        this.mjk
    },
    mjk(){
        axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search',{
                key_word:this.value,
                limit:10000
             }).then((res) => {
                    console.log(res.data.data);
                    this.list=res.data.data
                  });
    },
    onCancel() {
      this.$router.go(-1)
    },
    lilist(val){
        this.$router.push({
            path:'./xiangqing',
            query:{
                id:this.list[val]._id,
                cate_id:this.list[val].cate_id,
            }
        })
    }
  },

}
</script>

<style scoped>
ul{padding: 5px;}
ul li{width: 100%;border: 1px solid #ededed;line-height: 40px;}
</style>